'use strict'
import moment from 'moment'
import api from '@/api/api';

const CHANGE_NAME       = 'CHANGE_NAME',
      SET_WEEK          = 'SET_WEEK',
      GETTING_INFO      = 'GET_INFO',
      GET_INFO_SUCCESS  = 'GET_INFO_SUCCESS',
      CHANGE_DEPTO      = 'CHANGE_DEPTO';

export default {
    namespaced: true,
    state:{
        place: 'La Paz',
        department: 'Almacén',
        week: '',
        loading: false,
        data: [],
    },
    getters: {
        getName(state){
            return state.place;
        },
        getDep(state){
            return state.department;
        },
        getWeek(state){
            return state.week;
        },
        loading(state) {
            return state.loading;
        },
        data(state) {
            return state.data;
        }
    },
    mutations: {
        [CHANGE_NAME](state, payload){
            state.place = payload;
        },
        [CHANGE_DEPTO](state, payload){
            state.department = payload;
        },
        [SET_WEEK](state, payload){
            state.week = payload;
        },
        [GETTING_INFO](state) {
            state.loading = true;
            state.data = [];
        },
        [GET_INFO_SUCCESS](state, change) {
            state.loading = false;
            state.data = change;
        },
    },
    actions:{
        week({commit}){
            let startOfWeek = moment().startOf('isoweek').locale('es').format('dddd D [a]');
            let endOfWeek   = moment().endOf('isoweek').locale('es').format(' dddd D [de] MMMM[,] YYYY');
            let weeek = startOfWeek + endOfWeek;
            commit(SET_WEEK, weeek)
        },
        async getInfo({ commit }, city){
            commit(CHANGE_NAME, city)
            commit(GETTING_INFO);
            try {
                let link = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
                let response = await api.getDeptos(link);
                commit(GET_INFO_SUCCESS, response.data);
                return response.data;
            } catch (error) {
              console.log(error);
            }
        },
        changeDeptos({commit}, city){
            if(city == 'Juárez'){
                commit(CHANGE_DEPTO, 'Aligerante');
                let nCity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
                this.dispatch("plants/getBDInfo", {
                    department: 'Aligerante',
                    city: nCity
                });
                this.dispatch("plants/getAllInfo", {
                    department: 'Aligerante',
                    city: nCity
                });
            }else if(city == 'La Paz'){
                commit(CHANGE_DEPTO, 'Almacén');
                let nCity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
                this.dispatch("plants/getBDInfo", {
                    department: 'Almacén',
                    city: nCity
                });
                this.dispatch("plants/getAllInfo", {
                    department: 'Almacén',
                    city: nCity
                });
                this.dispatch('plants/getBonos', {
                    cityName: 'La Paz',
                    depName: 'Almacén'
                });
                this.dispatch('plants/getProduccion', {
                        cityName: 'La Paz',
                        depName: 'Almacén'
                });
            }else if(city == 'Cancún'){
                commit(CHANGE_DEPTO, 'Almacén');
                let nCity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
                this.dispatch("plants/getBDInfo", {
                    department: 'Almacén',
                    city: nCity
                });
                this.dispatch("plants/getAllInfo", {
                    department: 'Almacén',
                    city: nCity
                });
            }else if(city == 'Cd. México'){
                commit(CHANGE_DEPTO, 'Bloquera');
                let nCity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
                this.dispatch("plants/getBDInfo", {
                    department: 'Bloquera',
                    city: nCity
                });
                this.dispatch("plants/getAllInfo", {
                    department: 'Bloquera',
                    city: nCity
                });
            }else if(city == 'Culiacán'){
                commit(CHANGE_DEPTO, 'Corte');
                let nCity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
                this.dispatch("plants/getBDInfo", {
                    department: 'Corte',
                    city: nCity
                });
                this.dispatch("plants/getAllInfo", {
                    department: 'Corte',
                    city: nCity
                });
            }
        }
    }
}