import api from '../../api/api'

const UPDATE_SUCCESS    = 'UPDATE_SUCCESS',
      GETTING_INFO      = 'GET_INFO',
      GET_INFO_SUCCESS  = 'GET_INFO_SUCCESS',
      UPDATE_PROCESS    = 'UPDATE_PROCESS',
      GETTING_BD_INFO      = 'GETTING_BD_INFO',
      GET_BD_INFO_SUCCESS  = 'GET_BD_INFO_SUCCESS',
      GET_PRODDATE_SUCCESS  = 'GET_PRODDATE_SUCCESS',
      GET_PROD_SUCCESS  = 'GET_PROD_SUCCESS',
      GET_BONUSDATE_SUCCESS = 'GET_BONUSDATE_SUCCESS',
      GET_BONUS_SUCCESS = 'GET_BONUS_SUCCESS';

export default {
    namespaced: true,
    state:{
        notification: 'Guardar',
        loading: false,
        flag: false,
        data: [],
        bd: [],
        chartBonos: {},
        chartBonosFecha: {},
        chartProduccion: {},
        chartProduccionFecha: {},
    },
    getters: {
        getNotif(state){
            return state.notification;
        },
        loading(state) {
            return state.loading;
        },
        flag(state) {
            return state.flag;
        },
        data(state) {
            return state.data;
        },
        bd(state) {
            return state.bd;
        },
        chartBonos(state){
            return state.chartBonos;
        },
        chartBonosFecha(state){
            return state.chartBonosFecha;
        },
        chartProduccion(state){
            return state.chartProduccion;
        },
        chartProduccionFecha(state){
            return state.chartProduccionFecha;
        },
    },
    mutations: {
        [GETTING_INFO](state) {
            state.loading = true;
            state.data = [];
        },
        [GET_INFO_SUCCESS](state, change) {
            state.loading = false;
            state.data = change;
        },
        [UPDATE_SUCCESS](state, change){
            state.flag = true;
            state.notification = change;
        },
        [UPDATE_PROCESS](state){
            state.notification = 'Guardar';
            state.flag = false;
        },
        [GETTING_BD_INFO](state) {
            state.loading = true;
            state.bd = [];
        },
        [GET_BD_INFO_SUCCESS](state, change) {
            state.loading = false;
            state.bd = change;
        },
        [GET_PRODDATE_SUCCESS](state, change){
            state.chartProduccionFecha = change;
        },
        [GET_PROD_SUCCESS](state, change){
            state.chartProduccion = change;
        },
        [GET_BONUSDATE_SUCCESS](state, change){
            state.chartBonosFecha = change;
        },
        [GET_BONUS_SUCCESS](state, change){
            state.chartBonos = change;
        }
    },
    actions: {
        async editInfo({ commit }, payload) {
            commit(UPDATE_PROCESS);
            try {
              let response = await api.editInfo(payload.base, payload.dias_sucios, payload.extra_m3, payload.depto, payload.city);
              commit(UPDATE_SUCCESS, 'Cambios guardados');
              return response.data;
            } catch (error) {
              console.log(error);
            }
        },
        async getInfo({ commit }, payload) {
            commit(GETTING_INFO);
            let depto = payload.department;
            try {               
                let normalizedDepto = depto.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s]/g, "");                
                let response = await api.getDeptoInfo(normalizedDepto, payload.city, payload.userCode);
                commit(GET_INFO_SUCCESS, response.data);
                return response.data;
            } catch (error) {
                console.log(error);
            }
        },
        async getBDInfo({ commit }, payload) {    
            commit(GETTING_BD_INFO);
            let depto = payload.department;
            try {                                
                let normalizedDepto = depto.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s]/g, "");               
                let response = await api.getBDInfo(normalizedDepto, payload.city);
                commit(GET_BD_INFO_SUCCESS, response.data);
                return response.data;
            } catch (error) {
                console.log(error);
            }
        },
        async getAllInfo({ commit }, payload) {
            commit(GETTING_INFO);
            let depto = payload.department;
            try {                                
                let normalizedDepto = depto.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s]/g, "");
                let response = await api.getDashboardInfo(normalizedDepto, payload.city);
                commit(GET_INFO_SUCCESS, response.data);
                return response.data;
            } catch (error) {
                console.log(error);
            }
        },
        async getBonos({commit}, payload){
            try{
                let city = payload.cityName;
                let dep = payload.depName;
                let linkcity;
                let produccion = [];
                let fechas = [];
                let bonos = [];
               
                let linkdepto = dep.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s]/g, "");
                city == 'La Paz'? linkcity = city.toLowerCase().replace(/ /g,"") : linkcity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\ ]/g, "");

                if(linkcity != 'lapaz'){
                    linkcity =  'lapaz'
                }

                if(linkdepto != 'choferlocal' && linkdepto != 'corte' && linkdepto != 'bloquera'  && linkdepto != 'almacen' && linkdepto != 'vigueta'){
                    linkdepto = 'bloquera'
                }

                let response = await api.getBonos(linkdepto, linkcity);
                bonos = response.data.bonos;
                let len = bonos.length;

                for(let i = 0; i < len; i++){                    
                    fechas.push(bonos[i].semana)
                }
                commit(GET_BONUSDATE_SUCCESS, fechas);

                for(var i = 0; i < len; i++){
                    produccion.push(bonos[i].bono)
                }
                commit(GET_BONUS_SUCCESS, produccion);

            }catch(error){
                console.log(error);
            }
        },
        async getProduccion({commit}, payload){
            try{
                let city = payload.cityName;
                let dep = payload.depName;
                let linkcity;
                let produccion = [];
                let prod = [];
                let fechas = [];
                
                let linkdepto = dep.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\ ]/g, "");
                city == 'La Paz'? linkcity = city.toLowerCase().replace(/ /g,"") : linkcity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\ ]/g, "");
                if(linkcity != 'lapaz'){
                    linkcity =  'lapaz'
                }
                if(linkdepto != 'choferlocal' && linkdepto != 'corte' && linkdepto != 'bloquera'  && linkdepto != 'almacen' && linkdepto != 'vigueta'){
                    linkdepto = 'bloquera'
                }

                let response = await api.getProduccion(linkdepto, linkcity);
                produccion = response.data.produccion;
                let len = produccion.length;

                for(let i = 0; i < len; i++){                    
                    fechas.push(produccion[i].semana)
                }
                commit(GET_PRODDATE_SUCCESS, fechas);

                for(let i = 0; i < len; i++){
                    prod.push(produccion[i].produccion)
                }
                commit(GET_PROD_SUCCESS, prod);   
            }catch(error){
                console.log(error);
            }
        }
        
    }
}