import axios from "axios";

export default {
  getDeptos(city) {
    return axios.get(`/${city}`);
  },
  getDeptoInfo(depto, city, code) {
    return axios.get(`/${city}/${depto}/calculator/${code}`);
  },
  getDashboardInfo(depto, city) {
    return axios.get(`/${city}/${depto}/calculator`);
  },
  getBDInfo(depto, city) {
    return axios.get(`/${city}/${depto}`);
  },
  editInfo(base, dias_sucios, extra_m3, depto, city){
    return axios.put(`/${city}/admin/${depto}`,{
        base: base,
        dias_sucios: dias_sucios,
        extra_m3: extra_m3
    });
  },
  getBonos(depto,city) {
    return axios.get(`/${city}/${depto}/bonos`);
   },
  getProduccion(depto,city){
   return axios.get(`/${city}/${depto}/produccion`);
  },

  login(user, headers){
    return axios.post('/login', user, headers);
  }




};