import api from '@/api/api';
import jwt from 'jwt-simple';
// const jwt = require('jwt-simple');

const clave = 'fanosa-app2021-0x';

const SET_TOKEN     = 'SET_TOKEN',
      SET_IDENTITY  = 'SET_IDENTITY';

export default {
    namespaced: true,
    state:{
        token: '',
        identity: { },
    },
    getters: {
        getToken(state){
            return state.token;
        },
        getIdentity(state){
            return state.identity;
        }
    },
    mutations: {
        [SET_TOKEN](state, payload){
            state.token = payload;
        },
        [SET_IDENTITY](state, payload){
            state.identity = payload;
        }
    },
    actions:{
        async login({commit}, payload) {
            let email = payload.email,
                password = payload.password, 
                gettoken = payload.gettoken;

            if (gettoken != null) {
                gettoken = "true";
            }
            const user = { email, password, gettoken };
            try {
                let headers = { "Content-Type": "application/x-www-form-urlencoded" };
                let response = await api.login(user, headers);
                commit(SET_TOKEN, response.data.token);
                return response;
            } catch (error) {
                console.log(error);
            }
        },
        setToken({commit}, token) {
            localStorage.setItem("token", token);
            commit(SET_TOKEN, token);
        },
        getToken({commit}) {
            let userToken;
            let token = localStorage.getItem("token");
        
            if (token && token != "undefined") {
              userToken = token;
            } else {
              userToken = null;
            }
            commit(SET_TOKEN, userToken);
            return userToken;
        },
        decode({commit}, token){
            let decoded;
            let user = {};

            if(token != null){
             decoded = jwt.decode(token, clave);
             user  = JSON.stringify(decoded);
            }
            commit(SET_IDENTITY, user);
            return user;
        },
        logout() {
            localStorage.removeItem('identity');
            localStorage.removeItem('token');

            commit(SET_IDENTITY, null);
            commit(SET_TOKEN, null);
            this.$router.push("/login");
      },
    }
}